export function ChooseBlock() {
    const { t } = useTranslation('landings');
    const { location } = useHistory();
    const { generateUrl } = useNavigationUtils();
    const website = useWebsiteData();
    const featuresEnabled = useFeatures();
    const closePath = generateUrl('..');
    const filteredBlocks = location.state?.blocks || ALL_BLOCK_KINDS;
    const groups = useMemo(() => {
        const filter = new Set(filteredBlocks);
        const blockPredicate = ({ kind, features }) => filter.has(kind) && (!features || featuresEnabled(...features));

        return menu
            .map((group) => ({
                ...group,
                blocks: group.blocks.filter(blockPredicate),
            }))
            .filter((group) => !!group.blocks.length);
    }, [featuresEnabled, filteredBlocks.join(',')]);

    return (
        <ModalPane title={t('Page.View.ChooseElement.heading')} onClose={closePath}>
            {groups.map(({ group, label, blocks }) => (
                <BlockListItemPreview.List key={group}>
                    {label && (
                        <span style={{ margin: '12px 0 8px 10px', fontSize: 18, fontWeight: 'bold' }}>{t(label)}</span>
                    )}

                    {blocks.map((item) => (
                        <ChooseBlockListItem key={`${item.to}-${item.label}`} item={item} />
                    ))}
                </BlockListItemPreview.List>
            ))}

            <BlockRequestDialog style={{ marginTop: 16 }} websiteId={website._id} />
            {/*{(isFree || isTrial) && (*/}
            {/*    <p style={{ padding: '0 8px', margin: '16px 0', fontSize: 14, color: '#999' }}>*/}
            {/*        {t('Page.View.ChooseElement.proMessage')}*/}
            {/*    </p>*/}
            {/*)}*/}
        </ModalPane>
    );
}
