import React from 'react';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { __t } from '@mssgme/shared';
import { ToggleField, TextareaField, SmartLinkField } from '@mssgme/ui';
import { useWebsiteData } from '@mssgme/userpage-core';

import styles from './ShopcartSettingsForm.scss';

export const ShopcartSettingsForm = () => {
    const website = useWebsiteData();
    const { t } = useTranslation('products');
    const [{ value: form }] = useField('form');

    return (
        <>
            <div className={styles.section}>
                <p className={styles.sectionTitle}>{t('ProductsOnboarding.labels.sections.contactInfo')}</p>

                <ToggleField
                    builderInput
                    name="form.optional.lastName"
                    label={t('ProductsOnboarding.labels.optional.lastName')}
                />
            </div>

            <div className={styles.section}>
                <p className={styles.sectionTitle}>{t('ProductsOnboarding.labels.sections.deliveryAddress')}</p>

                <ToggleField
                    builderInput
                    name="form.collectAddress"
                    label={t('ProductsOnboarding.labels.collectAddress')}
                />

                <ToggleField
                    builderInput
                    name="form.optional.city"
                    disabled={!form.collectAddress}
                    label={t('ProductsOnboarding.labels.optional.city')}
                />

                <ToggleField
                    builderInput
                    name="form.optional.state"
                    disabled={!form.collectAddress}
                    label={t('ProductsOnboarding.labels.optional.state')}
                />

                <ToggleField
                    builderInput
                    name="form.optional.postalCode"
                    disabled={!form.collectAddress}
                    label={t('ProductsOnboarding.labels.optional.postalCode')}
                />
            </div>

            <div className={styles.section}>
                <p className={styles.sectionTitle}>{t('ProductsOnboarding.labels.sections.successScreen')}</p>

                <TextareaField
                    minRows={3}
                    maxLength={5000}
                    builderInput
                    name="form.successMessage"
                    label={t('ProductsOnboarding.labels.successMessage.label')}
                    placeholder={__t('payment_paid_subtitle')}
                />

                <SmartLinkField
                    className={styles.linkField}
                    titleName="form.successURL.title"
                    actionName="form.successURL.action"
                    website={website}
                    actionLabel={t('ProductsOnboarding.labels.successUrl.actionLabel')}
                    titleLabel={t('ProductsOnboarding.labels.successUrl.titleLabel')}
                    placeholder={t('ProductsOnboarding.labels.successUrl.placeholder')}
                />
            </div>
        </>
    );
};
