import { useCallback } from 'react';
import { useHistory } from 'react-router-dom';

import { getDefaultTheme, randomSlug } from '@mssgme/shared';
import { useFetching } from './useFetching';
import { useActions } from './useActions';

export const usePagesFactory = (websiteId) => {
    const history = useHistory();
    const { api } = useActions();
    const { run, ...state } = useFetching(async ({ templateId, theme } = {}) => {
        const name = 'Page';
        const result = await api.pages.create({
            websiteId,
            payload: {
                enabled: true,
                slug: randomSlug(5),
                theme: theme || getDefaultTheme(),
                name,
                templateId,
            },
        });

        history.push({
            pathname: `/pages/-/${websiteId}/${result._id}/setup`,
        });
    });

    const create = useCallback(({ templateId, theme }) => run({ templateId, theme }), []);

    return {
        run,
        create,
        ...state,
    };
};
