import { object, string, array, boolean, number, mixed } from 'yup';

import { normalizeWebsiteUrl } from '@mssgme/helpers';

export const SubscriptionFormItemSchema = object({
    type: mixed().oneOf(['phone', 'email', 'text', 'name', 'date', 'time', 'datetime', 'address', 'select', 'radio', 'checkbox', 'country', 'number']),
    label: string()
        .trim()
        .ensure()
        .min(3, 'common:errorsList.block_subform_item_label_length')
        .max(250, 'common:errorsList.block_subform_item_label_length'),
    required: boolean(),
    size: number(),
    options: string()
        .when('type', {
            is: (type) => ['select', 'radio', 'checkbox'].includes(type),
            then: string().required('common:errorsList.empty_field'),
        }),
});

export const SubscriptionFormBlockSchema = object({
    ctaLabel: string()
        .trim()
        .ensure()
        .max(250, 'common:errorsList.block_subform_cta_label_length'),
    ctaText: string()
        .trim()
        .ensure()
        .max(250, 'common:errorsList.block_subform_cta_text_length'),
    sendLabel: string()
        .trim()
        .ensure()
        // .required('common:errorsList.field_required')
        .max(250, 'common:errorsList.block_subform_send_label_length'),
    sentText: string()
        .trim()
        .ensure()
        // .required('common:errorsList.field_required')
        .max(1000, 'common:errorsList.block_subform_sent_text_length'),
    sentUrl: string()
        .nullable()
        .trim()
        .url('common:errorsList.enter_url')
        .max(800, 'common:errorsList.url_length')
        .transform((value) => value && normalizeWebsiteUrl(value)),
    items: array(SubscriptionFormItemSchema)
        .min(1, 'common:errorsList.block_subform_item_count_low')
        .max(20, 'common:errorsList.block_subform_item_count'),
});
