import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { Button, PortalModalSheet } from '@mssgme/ui';

import styles from './PromptModal.scss';

export function PromptModal({ handle, message, okText, ...rest }) {
    const { t } = useTranslation('landings');

    return (
        <PortalModalSheet fullWidth portalTo="#visual-builder" onClose={handle} {...rest}>
            {({ handleTrue, handleFalse }) => (
                <div>
                    <div className={styles.message}>{message}</div>

                    <Button fullWidth className={styles.horizontalButton} btnStyle="secondary2" onClick={handleFalse}>
                        {t('ui:button.cancel')}
                    </Button>
                    <Button fullWidth className={styles.horizontalButton} btnStyle="danger" onClick={handleTrue}>
                        {okText}
                    </Button>
                </div>
            )}
        </PortalModalSheet>
    );
}

PromptModal.propTypes = {
    handle: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    message: PropTypes.string.isRequired,
    okText: PropTypes.string.isRequired,
};
