import arrayMove from 'array-move';
import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useMount } from 'react-use';
import { useFormikContext } from 'formik';

import { renderNode } from '../../../helpers';
import { useDataFactoryState } from './useDataFactoryState';
import { ArrayItemEditModal } from './ArrayItem';
import { SortableItemsField } from './SortableItemsField';
import { ArrayItemAddButton } from './ArrayItemAddButton';

ArrayField.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.array,
    label: PropTypes.string,
    addButtonLabel: PropTypes.string,
    addMoreButtonLabel: PropTypes.string,
    modalTitle: PropTypes.string,
    context: PropTypes.any,
    sortable: PropTypes.bool,
    initialEdit: PropTypes.bool,
    isInitialDirty: PropTypes.bool,
    isEditable: PropTypes.bool,
    isDeletable: PropTypes.bool,
    addRestriction: PropTypes.string,
    Item: PropTypes.elementType.isRequired,
    children: PropTypes.any,
    getItemKey: PropTypes.func,
    getItemCollection: PropTypes.func,
    getItemSortable: PropTypes.func,
    factory: PropTypes.func,
    applyTransform: PropTypes.func,
    validationSchema: PropTypes.object,
    renderFactory: PropTypes.func,
    maxItems: PropTypes.number,
};

ArrayField.defaultProps = {
    sortable: true,
    labels: {},
    isEditable: true,
    isDeletable: true,
    isInitialDirty: true,
};

export default function ArrayField({
    name,
    value,
    initialEdit,
    isInitialDirty,
    isEditable,
    isDeletable,
    sortable,
    Item,
    applyTransform,
    getItemKey,
    getItemCollection,
    getItemSortable,
    factory: rawFactory,
    children,
    label,
    addButtonLabel,
    modalTitle,
    addMoreButtonLabel,
    validationSchema,
    renderFactory,
    maxItems,
    addRestriction,
    context,
    ...rest
}) {
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const items = value || getFieldMeta(name).value || [];
    const currentTotal = items.length;
    const [editingIndex, setEditingIndex] = useState((initialEdit && !currentTotal) ? 0 : -1);
    const [factory, safeSetFactory] = useDataFactoryState(rawFactory);
    const isEditing = isEditable && editingIndex >= 0;
    const isNew = currentTotal === editingIndex;
    const canAddMore = isEditable && ((!maxItems) || maxItems > currentTotal);

    useMount(() => {
        if (initialEdit) {
            handleAdd();
        }
    }, [initialEdit]);

    const handleAdd = useCallback(() => setEditingIndex(currentTotal), [currentTotal]);
    const handleEdit = useCallback(
        (item) => {
            safeSetFactory(item);
            setEditingIndex(items.indexOf(item));
        },
        [items]
    );
    const handleApply = useCallback(
        (values) => {
            setEditingIndex(-1);

            if (rawFactory) {
                safeSetFactory(rawFactory);
            } else {
                safeSetFactory(null);
            }

            if (values) {
                let transformed;

                if (applyTransform) {
                    if (false !== (transformed = applyTransform(values, editingIndex))) {
                        values = transformed || values;
                    } else {
                        return;
                    }
                }

                setFieldValue(`${name}.${editingIndex}`, values, true);
                setFieldTouched(`${name}.${editingIndex}`, true, true);
            }
        },
        [editingIndex, applyTransform, rawFactory]
    );
    const handleDeletion = useCallback(
        (item) => {
            setFieldValue(name, items.filter((i) => i !== item), true);
            setFieldTouched(name, true, true);
            setEditingIndex(-1);
        },
        [name, items]
    );

    const onSortStart = useCallback((_, e) => e?.preventDefault?.(), []);
    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (!sortable || oldIndex === newIndex) {
                return;
            }

            setFieldValue(name, arrayMove(items, oldIndex, newIndex));
        },
        [sortable, items]
    );

    return (
        <div {...rest}>
            <SortableItemsField
                name={name}
                label={label}
                items={items}
                sortable={sortable}
                Item={Item}
                context={context}
                getItemKey={getItemKey}
                getItemCollection={getItemCollection}
                getItemSortable={getItemSortable}
                onSortStart={onSortStart}
                onSortEnd={onSortEnd}
                onEdit={isEditable ? handleEdit : null}
                onDelete={isDeletable ? handleDeletion : null}
            />

            {canAddMore && (
                <ArrayItemAddButton
                    name={name}
                    items={items}
                    addButtonLabel={addButtonLabel}
                    addMoreButtonLabel={addMoreButtonLabel}
                    disabled={isEditing}
                    restriction={addRestriction}
                    onClick={handleAdd}
                />
            )}

            {isEditing && (
                factory ? (
                    <ArrayItemEditModal
                        fullWidth
                        title={modalTitle}
                        isNew={isNew}
                        isInitialDirty={isInitialDirty}
                        name={`${name}.${editingIndex}`}
                        factory={factory}
                        validationSchema={validationSchema}
                        onApply={handleApply}
                        onDelete={isDeletable ? handleDeletion : null}
                    >
                        {children}
                    </ArrayItemEditModal>
                ) : (
                    renderNode(renderFactory, { setFactory: safeSetFactory, setEditingIndex })
                )
            )}
        </div>
    );
}
