import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { Button, SubmitButton } from '@mssgme/ui';
import { useProductsContext } from '../../../../hooks';

import styles from '../ProductsOnboarding.scss';

export const StepFinished = () => {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();

    return (
        <div className={styles.stepContainer} style={{ justifyContent: 'flex-end' }}>
            <SubmitButton
                fullWidth
                style={{ marginTop: 16 }}
                btnStyle="secondary"
                isInitialHidden={false}
                disabled={false}
            >
                {t('ProductsOnboarding.labels.repeat')}
            </SubmitButton>

            <Button fullWidth style={{ marginTop: 16 }} tag={Link} to={router.toHome()}>
                {t('ui:labels.close')}
            </Button>
        </div>
    );
};
