import PropTypes from 'prop-types';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useHistory } from 'react-router-dom';

import { StreamlinedScreen } from '@mssgme/ui';
import { useActions } from '@mssgme/hooks';
import { useProductsContext } from '../../../hooks';
import { ProductsSettingsForm } from '../ProductsSettingsForm';
import { makeProductSettingsWizard } from './Steps';
import { StepsCounter } from './StepsCounter';

import styles from './ProductsOnboarding.scss';

export function ProductsOnboarding({ fromStart }) {
    const history = useHistory();
    const startRef = useRef(fromStart);
    const wizard = useMemo(() => makeProductSettingsWizard());
    const { router, crm, specifier } = useProductsContext();
    const { api } = useActions();
    const [step, setStep] = useState(() =>
        wizard.isFinalStep(crm.onBoardingStep)
            ? wizard.firstStep()
            : startRef.current
                ? wizard.firstStep()
                : crm.onBoardingStep
    );
    const Step = wizard.getForm(step);
    const isFinished = wizard.isFinalStep(step);

    const handleBackPath = async () => {
        await api.websites.crm.settings.update({
            websiteId: specifier.website,
            payload: { ...crm, onBoarding: false },
        });

        history.push(router.toHome());
    };

    useEffect(() => {
        if (!startRef.current) {
            setStep(crm.onBoardingStep);
        }

        startRef.current = false;
    }, [crm]);

    return (
        <StreamlinedScreen modal inverse={false} nopadding={false} backPath={handleBackPath}>
            <ProductsSettingsForm
                initialData={{ onBoarding: false }}
                showSubmit={!isFinished}
                successRedirectPath={router.settings.toOnboarding()}
                doRedirect={false}
                initialDirty
            >
                <StepsCounter step={step} />

                <div className={styles.stepContainer}>
                    <Step />
                </div>
            </ProductsSettingsForm>
        </StreamlinedScreen>
    );
}

ProductsOnboarding.propTypes = {
    fromStart: PropTypes.any,
};
