import PropTypes from 'prop-types';
import React, { useEffect, useMemo } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { makeProductSettingsWizard } from './Steps';

import styles from './ProductsOnboarding.scss';

export const StepsCounter = ({ step }) => {
    const { t } = useTranslation('products');
    const { values, setFieldValue } = useFormikContext();
    const wizard = useMemo(() => makeProductSettingsWizard());
    const next = wizard.nextStep(step, values);
    const isFinished = wizard.isFinalStep(step);

    useEffect(() => {
        if (values.onBoardingStep !== next) {
            setFieldValue('onBoardingStep', next);
        }
    }, [values.onBoardingStep, next]);

    return (
        <div className={styles.intro} style={{ marginTop: -16 }}>
            <h1>{t('ProductsOnboarding.title')}</h1>

            {!isFinished && (
                <span className={styles.step}>
                    {t('ProductsOnboarding.steps', {
                        step: wizard.stepNumber(step, values),
                        total: wizard.stepsCount(step, values),
                    })}
                </span>
            )}
            <p>{t(`ProductsOnboarding.stepTips.${step}`)}</p>
        </div>
    );
};

StepsCounter.propTypes = {
    step: PropTypes.string.isRequired,
};
