import {
    CurrencySettingsForm,
    MessengersSettingsForm,
    NotificationsSettingsForm,
    PaymentsSettingsForm,
    ShopcartSettingsForm,
} from '../../forms';
import { StepFinished } from './StepFinished';

export const makeProductSettingsWizard = () => {
    const stepForms = {
        currency: CurrencySettingsForm,
        messengers: MessengersSettingsForm,
        payments: PaymentsSettingsForm,
        form: ShopcartSettingsForm,
        notifications: NotificationsSettingsForm,
        finished: StepFinished,
    };

    const finishedStep = 'finished';
    const stepsOrder = ['currency', 'messengers', 'payments', 'form', 'notifications', finishedStep];
    const paymentsDependantSteps = ['form', 'notifications'];
    const totalSteps = stepsOrder.length - 1;
    const isFinalStep = (step) => stepsOrder.indexOf(step) >= stepsOrder.length - 1;
    const paymentMethodsEnabled = (crm) => Object.values(crm.payments).some(({ enabled }) => enabled);
    const paymentsIdx = stepsOrder.indexOf('payments');
    const adjusted = totalSteps - paymentsDependantSteps.length;

    return {
        stepsCount: (step, crm) => {
            const isPaymentMethodsEnabled = paymentMethodsEnabled(crm);
            const idx = stepsOrder.indexOf(step);

            if (!isPaymentMethodsEnabled && (idx <= paymentsIdx || isFinalStep(step))) {
                return adjusted;
            }

            return totalSteps;
        },
        stepNumber: (step, crm) => {
            const isPaymentMethodsEnabled = paymentMethodsEnabled(crm);
            const idx = stepsOrder.indexOf(step);

            if (!isPaymentMethodsEnabled && isFinalStep(step)) {
                return adjusted;
            }

            return idx + 1;
        },
        firstStep: () => stepsOrder[0],
        nextStep: (step, crm) => {
            const isPaymentMethodsEnabled = paymentMethodsEnabled(crm);
            const idx = stepsOrder.indexOf(step);
            const next = idx + 1;

            if (!isPaymentMethodsEnabled && idx >= paymentsIdx && !isFinalStep(step)) {
                return finishedStep;
            }

            return stepsOrder[next] || stepsOrder[0];
        },
        getForm: (step) => stepForms[step],
        isFinalStep,
    };
};
