import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { useUpdateEffect } from 'react-use';

import { normalizeRawUrl } from '@mssgme/helpers';
import { useEventCallback } from '@mssgme/hooks';
import { LandingsPropTypes } from '../../domainTypes';
import TextField from './TextField';
import SelectField from './SelectField';
import SelectPageField from './SelectPageField';

const ALL_TYPES = ['url', 'page', 'tel', 'email', 'sms', 'fullImage', 'nothing'];
const TYPES = {
    all: ALL_TYPES,
    image: ALL_TYPES,
    link: ['url', 'page', 'tel', 'email', 'sms'],
};
const TEXT_INPUT_TYPES_MAP = {
    url: 'text',
    tel: 'tel',
    sms: 'tel',
    email: 'email',
};
const typeOptions = (t, withPages, blockKind) => {
    const candidates = TYPES[blockKind];
    const useTypes = withPages ? candidates : candidates.filter((value) => value !== 'page');

    return useTypes.map((value) => ({
        label: t(`ActionField.types.${value}`),
        value,
    }));
};

const urlLabelsMap = (t, type) => {
    return t(`ActionField.inputs.${type}`, { returnObjects: true });
};

export default function ActionField({
    name,
    website,
    blockKind = 'all',
    actionLabel = '',
    urlLabel = '',
    required = false,
    ...rest
}) {
    const { t } = useTranslation('ui');
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value } = getFieldMeta(name);
    const type = value.type;
    const isPage = type === 'page';
    const isUrl = type === 'url';
    const isMultiPage = website?.pages.length > 1;
    const isTypeWithInput = type in TEXT_INPUT_TYPES_MAP;
    const options = useMemo(() => typeOptions(t, isMultiPage, blockKind), [t, isMultiPage, blockKind]);
    const actionValueLabels = useMemo(() => urlLabelsMap(t, type), [t, type]);

    const handleNormalizeUrl = useEventCallback(() => {
        const url = normalizeRawUrl(value.url || '');

        if (url && url !== value?.url) {
            setFieldValue(`${name}.url`, url);
        }
    });

    useUpdateEffect(() => {
        setFieldValue(`${name}.url`, '');
        setFieldValue(`${name}.page`, null);
    }, [type]);

    return (
        <div {...rest}>
            <SelectField
                builderInput
                required={required}
                name={`${name}.type`}
                label={actionLabel || t('ActionField.action')}
                options={options}
            />

            {isPage && (
                <SelectPageField
                    required={required}
                    builderInput
                    website={website}
                    name={`${name}.page`}
                    label={actionValueLabels.label}
                    placeholder={actionValueLabels.placeholder}
                />
            )}

            {isTypeWithInput && (
                <TextField
                    required={required}
                    builderInput
                    autoCapitalize="off"
                    name={`${name}.url`}
                    label={urlLabel || actionValueLabels.label}
                    placeholder={actionValueLabels.placeholder}
                    onBlur={isUrl ? handleNormalizeUrl : undefined}
                    type={TEXT_INPUT_TYPES_MAP[type]}
                />
            )}
        </div>
    );
}

ActionField.propTypes = {
    name: PropTypes.string.isRequired,
    website: LandingsPropTypes.website,
    blockKind: PropTypes.oneOf(['all', 'link', 'image']),
    actionLabel: PropTypes.string,
    urlLabel: LandingsPropTypes.inputLabel,
    required: PropTypes.bool,
};
