import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { BlockListItemPreview, PortalModalSheet, ArrayField, TextareaField, TextField, ToggleField } from '@mssgme/ui';
import { SubscriptionFormItemSchema } from '../components';
import { SUBSCRIPTION_FORM_ITEM_TYPES, SubscriptionFormItem } from './SubscriptionFormItem';

const SIZE_NORMAL = 250;
const SIZE_LARGE = 5000;

const isLarge = (size) => size > SIZE_NORMAL;
const getSize = (large) => large ? SIZE_LARGE : SIZE_NORMAL;

const TYPES_WITH_USER_OPTIONS = ['select', 'radio', 'checkbox'];

export const SubscriptionFormItemsField = (props) => {
    const { t } = useTranslation('landings');
    const handleFactory = useCallback(({ setFactory, setEditingIndex, ...rest }) => {
        if (rest.type) {
            setFactory(() => {
                const large = !!(rest.large || (rest.size && isLarge(rest.size)));

                return {
                    label: '',
                    required: true,
                    large,
                    size: getSize(large),
                    options: '',
                    ...rest,
                };
            });
        } else {
            setEditingIndex(-1);
        }
    }, []);

    return (
        <ArrayField
            maxItems={20}
            addButtonLabel={t('Page.Edit.SubscriptionForm.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.SubscriptionForm.itemAddMore')}
            modalTitle={t('Page.Edit.SubscriptionForm.itemFormHeading')}
            Item={SubscriptionFormItem}
            validationSchema={SubscriptionFormItemSchema}
            addRestriction="subscription-form-field:create"
            isInitialDirty
            renderFactory={(callbacks) => (
                <PortalModalSheet
                    onClose={(template) => handleFactory({ ...template, ...callbacks })}
                    title={t('Page.Edit.SubscriptionForm.fieldTypesModal.title')}
                >
                    {({ handleClose }) => (
                        <BlockListItemPreview.List>
                            {Object.entries(SUBSCRIPTION_FORM_ITEM_TYPES).map(([type, { icon, props }]) => (
                                <BlockListItemPreview
                                    key={type}
                                    labelIcon={icon}
                                    label={t(`Page.Edit.SubscriptionForm.fieldTypes.${type}.label`)}
                                    restrict={{ rule: 'subscription-form-field:create', subject: { items: [{ type }] } }}
                                    onClick={() =>
                                        handleClose({
                                            type,
                                            label: t(`Page.Edit.SubscriptionForm.fieldTypes.${type}.initialValue`, ''),
                                            ...props,
                                        })
                                    }
                                />
                            ))}
                        </BlockListItemPreview.List>
                    )}
                </PortalModalSheet>
            )}
            {...props}
        >
            {({ values, setFieldValue }) => (
                <div>
                    <TextField
                        builderInput
                        name="label"
                        label={t('Page.Edit.SubscriptionForm.fields.label.label')}
                        placeholder={t('Page.Edit.SubscriptionForm.fields.label.placeholder')}
                        maxLength={250}
                    />

                    {TYPES_WITH_USER_OPTIONS.includes(values.type) &&
                        <TextareaField
                            builderInput
                            name="options"
                            label={t('Page.Edit.SubscriptionForm.fields.options.label')}
                            placeholder={t('Page.Edit.SubscriptionForm.fields.options.placeholder')}
                            maxLength={2000}
                            minRows={8}
                            maxRows={16}
                        />
                    }

                    <ToggleField
                        builderInput
                        name="required"
                        label={t('Page.Edit.SubscriptionForm.fields.required.label')}
                        placeholder={t('Page.Edit.SubscriptionForm.fields.required.placeholder')}
                    />

                    {values.type === 'text' && (
                        <ToggleField
                            builderInput
                            name="large"
                            value={isLarge(values.size)}
                            label={t('Page.Edit.SubscriptionForm.fields.large.label')}
                            placeholder={t('Page.Edit.SubscriptionForm.fields.large.placeholder')}
                            onChange={(_, large) => {
                                setFieldValue('size', getSize(large));
                            }}
                        />
                    )}
                </div>
            )}
        </ArrayField>
    );
};

SubscriptionFormItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
