import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { LandingsPropTypes } from '../../domainTypes';
import { SelectPageInput } from '../Inputs';
import FieldRow from './FieldRow';

export default function SelectPageField({ website, onChange, ...rest }) {
    const { t } = useTranslation('ui');
    const [, { initialValue }, { setValue, setTouched }] = useField(rest.name);

    const handleChange = useCallback((value) => {
        setValue(value);
        setTouched(value !== initialValue);

        onChange?.(value);
    }, [initialValue, onChange]);

    return (
        <FieldRow {...rest}>
            {({ field: { name, value, disabled, ...rest } }) => (
                <SelectPageInput
                    {...rest}
                    placeholder={t('SelectPageField.placeholder')}
                    name={name}
                    disabled={disabled}
                    value={value}
                    website={website}
                    onChange={handleChange}
                />
            )}
        </FieldRow>
    );
}

SelectPageField.propTypes = {
    name: PropTypes.string.isRequired,
    website: LandingsPropTypes.website.isRequired,
    onChange: PropTypes.func,
};
