import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useFormikContext } from 'formik';

import { useFeature } from '@mssgme/hooks';
import { FieldRow } from '@mssgme/ui';

import { PaymentSettingsField } from './PaymentSettingsField';
import { PaymentSettingsItemEditModal } from './PaymentSettingsItemEditModal';

import styles from './PaymentsSettingsField.scss';

const PROVIDERS = [
    'cod',
    'paypal',
    'stripe',
    'wayforpay',
    // 'robokassa',
    'liqpay',
    'other',
    // 'transfer',
];

export const PaymentsSettingsField = ({ name, ...rest }) => {
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const { value: payments } = getFieldMeta(name);
    const [editingProvider, setEditingProvider] = useState();
    const otherEnabled = useFeature('payments_other');

    const handleDeletion = useCallback(
        (provider, values) => {
            setFieldValue(name, { ...payments, [provider]: values || { ...payments[provider], enabled: false } });
            setFieldTouched(name, true, true);
        },
        [name, payments]
    );
    const handleApply = useCallback(
        (values) => {
            if (values) {
                if (!values.enabled) {
                    handleDeletion(editingProvider, values);
                } else {
                    setFieldValue(`${name}.${editingProvider}`, values, true);
                    setFieldTouched(`${name}.${editingProvider}`, true, true);
                }
            }

            setEditingProvider(null);
        },
        [editingProvider]
    );

    return (
        <FieldRow className={styles.root} name={name} {...rest}>
            <ul className={styles.list}>
                {PROVIDERS.map(
                    (provider) =>
                        (provider !== 'other' || otherEnabled || payments[provider].enabled) && (
                            <PaymentSettingsField
                                key={provider}
                                provider={provider}
                                name={`${name}.${provider}`}
                                onEdit={setEditingProvider}
                                onDelete={handleDeletion}
                            />
                        )
                )}
            </ul>

            {editingProvider && (
                <PaymentSettingsItemEditModal
                    fullWidth
                    name={`${name}.${editingProvider}`}
                    provider={editingProvider}
                    onApply={handleApply}
                />
            )}
        </FieldRow>
    );
};

PaymentsSettingsField.propTypes = {
    name: PropTypes.string.isRequired,
};
