import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { FiAlertTriangle, FiPlus, FiTrash2 } from 'react-icons/fi';

import { FieldRow, FieldTip, PortalModalSheet } from '@mssgme/ui';
import { usePaymentsConfigValidity } from '../../../hooks';
import { PAYMENT_PROVIDER_FORMS } from './providers';

import styles from './PaymentsSettingsField.scss';

export const PaymentSettingsField = ({ name, provider, onEdit, onDelete, ...rest }) => {
    const { t } = useTranslation('products');
    const { getFieldMeta } = useFormikContext();
    const { value } = getFieldMeta(name);
    const { icon: Icon, title } = PAYMENT_PROVIDER_FORMS[provider];
    const { valid, tip } = usePaymentsConfigValidity(provider);
    const [isTipped, toggleTip] = useToggle();
    const isNew = !value.enabled;

    const handleEdit = useCallback(() => {
        onEdit(provider);
        // if (valid || !isNew) {
        //     onEdit(provider);
        // } else {
        //     toggleTip(true);
        // }
    }, [isNew, valid, provider, onEdit]);
    const handleRemove = useCallback(() => onDelete(provider), [provider, onDelete]);

    return (
        <FieldRow name={name} className={styles.field} {...rest}>
            <div className={cn(styles.row, styles.small)}>
                <div className={styles.left}>
                    {Icon && (
                        <div className={styles.iconWrapper} onClick={handleEdit}>
                            <Icon />
                        </div>
                    )}
                </div>

                <div className={cn(styles.value, !isNew && styles.active)}>{t(`Payments.${title}.heading`)}</div>

                {/*<div className={styles.previewValue}>{t('Value')}</div>*/}

                <div className={styles.buttons}>
                    {!valid && (
                        <div onClick={toggleTip} className={cn(styles.button, styles.danger)} title={tip}>
                            <FiAlertTriangle size={16} />
                        </div>
                    )}
                    {!isNew ? (
                        <div onClick={handleRemove} className={cn(styles.button, styles.remove)}>
                            <FiTrash2 size={20} />
                        </div>
                    ) : (
                        <div onClick={handleEdit} className={cn(styles.button)}>
                            <FiPlus size={20} />
                        </div>
                    )}
                </div>
                <div className={styles.layer} onClick={handleEdit} />

                {isTipped && (
                    <PortalModalSheet title={t(`Payments.${title}.heading`)} onClose={toggleTip}>
                        <FieldTip styled="warning">{tip}</FieldTip>
                    </PortalModalSheet>
                )}
            </div>
        </FieldRow>
    );
};

PaymentSettingsField.propTypes = {
    name: PropTypes.string.isRequired,
    provider: PropTypes.string.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
};
