import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';

import { useRestriction } from '@mssgme/hooks';
import { Button, EnhancedFormik, PortalModalSheet, ToggleField } from '@mssgme/ui';
import { PAYMENT_PROVIDER_FORMS } from './providers';

export const PaymentSettingsItemEditModal = ({ name, provider, onApply, ...rest }) => {
    const { t } = useTranslation('products');
    const { getFieldMeta } = useFormikContext();
    const { value: value = { enabled: true } } = getFieldMeta(name);
    const { title, form: Form, schema } = PAYMENT_PROVIDER_FORMS[provider];
    const { handleBlockedEvent } = useRestriction('payments', value);

    return (
        <PortalModalSheet
            title={t(`Payments.${title}.heading`)}
            onClose={onApply}
            render={({ handleClose }) => (
                <EnhancedFormik initialValues={value} validationSchema={schema} onSubmit={handleClose}>
                    {({ values, dirty, submitForm }) => (
                        <div>
                            <ToggleField
                                builderInput
                                name="enabled"
                                label={t(`fields.payments.${provider}.enabled.label`)}
                            />

                            {Form && <Form enabled={values.enabled} />}

                            <Button
                                fullWidth
                                size="standart"
                                style={{ marginTop: 16 }}
                                disabled={!dirty}
                                onClick={handleBlockedEvent || submitForm}
                            >
                                {t('ui:labels.apply')}
                            </Button>
                        </div>
                    )}
                </EnhancedFormik>
            )}
            {...rest}
        />
    );
};

PaymentSettingsItemEditModal.propTypes = {
    name: PropTypes.string.isRequired,
    provider: PropTypes.string.isRequired,
    onApply: PropTypes.func.isRequired,
};
